import TableTree from './table-tree.vue'
import TableTreeColumn from './column'

const components = [
  TableTree,
  TableTreeColumn
]

const install = (Vue) => {
  components.forEach(component => {
    Vue.component(component.name, component)
  })
}

export default {
  version: '0.1.0',
  install,
  ...components
}
